import { ensureElement } from "../../utils/utils";
import { Component } from "./Component";
export class BasketItem extends Component {
    constructor(container, _handlers) {
        super(container);
        this.__orderNumberElement = ensureElement('.basket__item-index', this.container);
        this.__titleElement = ensureElement('.card__title', this.container);
        this.__priceElement = ensureElement('.card__price', this.container);
        this.__removeButtonElement = ensureElement('.basket__item-delete', this.container);
        if (_handlers.onRemove) {
            this.__removeButtonElement.addEventListener('click', _handlers.onRemove);
        }
    }
    set title(title) {
        this.setText(this.__titleElement, title);
    }
    set price(price) {
        const text = (price) ? `${price} синапсов` : 'Бесценно';
        this.setText(this.__priceElement, text);
    }
    set index(orderNumber) {
        this.setText(this.__orderNumberElement, orderNumber);
    }
}
