import { ensureElement } from "../../utils/utils";
import { Component } from "./Component";
export class Basket extends Component {
    constructor(container, _handlers) {
        super(container);
        this.container = container;
        this._basketListElement = ensureElement('.basket__list', this.container);
        this._basketButtonElement = ensureElement('.basket__button', this.container);
        this._totalSumElement = ensureElement('.basket__price', this.container);
        if (_handlers.onBuy) {
            this._basketButtonElement.addEventListener('click', _handlers.onBuy);
        }
    }
    clear() {
        this._basketListElement.innerHTML = null;
    }
    set disableBuyButton(isDisabled) {
        this.setDisabled(this._basketButtonElement, isDisabled);
    }
    set totalSum(totalSum) {
        this.setText(this._totalSumElement, `${totalSum} синапсов`);
    }
    set items(items) {
        this._basketListElement.replaceChildren(...items);
    }
}
