import { cloneTemplate, ensureElement } from "../../utils/utils";
import { Card } from "./Card";
export class PopupCard extends Card {
    constructor(events) {
        super(cloneTemplate('#card-preview'), {
            onClick: () => events.emit('catalogCard:addToBasket', { id: this.id }),
        });
        this.__descriptionElement = ensureElement('.card__text', this.container);
    }
    set id(id) {
        this.container.dataset.id = id || '';
    }
    get id() {
        return this.container.dataset.id || '';
    }
    set description(description) {
        this.setText(this.__descriptionElement, description);
    }
    set isDisabled(isDisable) {
        this.setDisabled(this._buttonElement, isDisable);
    }
}
