import { ensureElement } from "../../utils/utils";
import { Component } from "./Component";
export class Card extends Component {
    constructor(container, _handlers) {
        super(container);
        this.__categoryElement = ensureElement('.card__category', this.container);
        this.__titleElement = ensureElement('.card__title', this.container);
        this.__imageElement = ensureElement('.card__image', this.container);
        this.__priceElement = ensureElement('.card__price', this.container);
        this._buttonElement =
            this.container.querySelector('.card__button');
        if (_handlers) {
            if (this._buttonElement) {
                this._buttonElement.addEventListener('click', _handlers.onClick);
            }
            else
                this.container.addEventListener('click', _handlers.onClick);
        }
    }
    set category(category) {
        this.setText(this.__categoryElement, category);
        let modifier;
        switch (category) {
            case 'софт-скил': {
                modifier = 'soft';
                break;
            }
            case 'хард-скил': {
                modifier = 'hard';
                break;
            }
            case 'другое': {
                modifier = 'other';
                break;
            }
            case 'дополнительное': {
                modifier = 'additional';
                break;
            }
            case 'кнопка': {
                modifier = 'button';
                break;
            }
        }
        this.__categoryElement.classList.add('card__category', `card__category_${modifier}`);
    }
    set title(title) {
        this.setText(this.__titleElement, title);
    }
    set image(src) {
        this.setImage(this.__imageElement, src);
    }
    set price(price) {
        const text = price ? `${price} синапсов` : 'Бесценно';
        this.setText(this.__priceElement, text);
    }
}
