import { Api } from '../base/api';
import { API_URL, CDN_URL } from '../../utils/constants';
export class ServerApi extends Api {
    constructor() {
        super(API_URL);
    }
    _completeProductInfo(product) {
        return Object.assign(Object.assign({}, product), { image: `${CDN_URL}/${product.image}` });
    }
    async getProductList() {
        const productList = (await this.get('/product'));
        const products = productList.items.map(this._completeProductInfo);
        return Object.assign(Object.assign({}, productList), { items: products });
    }
    async getProductItem(id) {
        const product = (await this.get(`/product/${id}`));
        return this._completeProductInfo(product);
    }
    postOrder(orderInfo) {
        return this.post('/order', orderInfo);
    }
}
